import { drawRadiusRect, window2Canvas } from './util'
import { BOARD_GRID_COLOR, BOARD_GRID_TYPE_DEFAULT } from './constant'

/**
 * 
 *
 * @export
 * @class BoardGrid
 */
export default class BoardGrid {
  /**
   * 
   *
   * @param {Number} boardGridX 
   * @param {Number} boardGridY 
   * @param {Number} boardGridSize 
   * @param {Number} boardGridType 
   * @param {Number} boardGridChess 
   * @param {Number} boardGridRadius 
   * @memberof BoardGrid
   */
  constructor (
    boardGridX,
    boardGridY,
    boardGridSize,
    boardGridType,
    boardGridChess,
    boardGridRadius
  ) {
    // 
    this.boardGridX = boardGridX
    this.boardGridY = boardGridY
    // 
    this.boardGridSize = boardGridSize
    // 
    this.boardGridType = boardGridType
    // 
    this.boardGridChess = boardGridChess
    // 
    this.boardGridRadius = boardGridRadius
  }

  setBoardGridPosition (x, y) {
    this.boardGridX = x
    this.boardGridY = y
  }

  setBoardGridSize (boardGridSize) {
    this.boardGridSize = boardGridSize
  }

  redrawBoardGrid (
    ctx
  ) {
    this.drawBoardGrid(ctx)
  }

  redrawBoardGridChess (ctx) {
    if (this.boardGridType !== BOARD_GRID_TYPE_DEFAULT) {
      this.drawBoardGridChess(ctx)
    }
  }

  /**
   * 
   *
   * @param {Number} clientX
   * @param {Number} clientY
   * @param {Object} gameCanvas
   * @returns
   * @memberof BoardGrid
   */
  isInBoardGird (clientX, clientY, gameCanvas) {
    // event.offsetX,event.offsetYAPIdprAPI
    const { x, y } = window2Canvas(gameCanvas.canvas, clientX, clientY)
    drawRadiusRect(
      this.boardGridX,
      this.boardGridY,
      this.boardGridSize,
      this.boardGridSize,
      this.boardGridRadius,
      gameCanvas.context
    )
    return gameCanvas.context.isPointInPath(x, y)
  }

  /**
   * 
   *
   * @param {Object} ctx 
   * @memberof BoardGrid
   */
  drawBoardGrid (ctx) {
    drawRadiusRect(
      this.boardGridX,
      this.boardGridY,
      this.boardGridSize,
      this.boardGridSize,
      this.boardGridRadius,
      ctx
    )
    ctx.fillStyle = BOARD_GRID_COLOR
    ctx.fill()
  }

  /**
   * 
   *
   * @param {Object} gameChess 
   * @param {Object} ctx 
   * @memberof BoardGrid
   */
  drawBoardGridChess (ctx) {
    this.boardGridChess.drawChess(
      this.boardGridX,
      this.boardGridY,
      this.boardGridSize,
      ctx
    )
  }

  setBoardGridChess (gameChessType, ctx) {
    this.boardGridChess.chessType = gameChessType
    this.drawBoardGridChess(ctx)
    this.boardGridType = gameChessType
  }

  setBoardGridChessProperty (chessSize, chessLineWidth) {
    // 
    this.boardGridChess.setChessSize(
      chessSize
    )
    this.boardGridChess.setChessLineWidth(
      chessLineWidth
    )
  }
}
